@import url(https://cdn.jsdelivr.net/gh/rastikerdar/shabnam-font@v5.0.1/dist/font-face.css);

html, body {
	margin: 0;
	padding: 0;
}
* {
	box-sizing: border-box;
}

body {
	font-family: Shabnam;
	direction: rtl;
	text-align: right;
	background-color: rgba(9, 9, 9, .04);
}

input, button {
	font-family: Shabnam;
}